html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 15px;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

main {
  background-color: #090b1a;
  width: 100vw;
  height: 100vh;
}

.card {
  font-family: "Inter", sans-serif;
  background-color: #1b1938;
  overflow: hidden;
  width: 1100px;
  height: 450px;
  border-radius: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: row-reverse;
}
.card-body {
  padding: 75px;
  padding-right: 120px;
}
.card span {
  color: #aa5cdb;
}
.card h1,
.card h3 {
  color: white;
}
.card p {
  color: rgba(255, 255, 255, 0.75);
}
.card div > p {
  color: rgba(255, 255, 255, 0.6);
}